/**
 * @param {headimgurl} 
 * @param {nickname} 
 * @param {openid} openid
 * @param {YHID} ID
 * @param {NAME}  
 * @param {PHONENO} 
 * @param {SFZH} 
 * @param {SMRZZT} 
 * @param {STATUS} 
 */
// eslint-disable-next-line
 export function setUserInfo(headimgurl, nickname, openid, YHID, NAME, PHONENO,  SFZH, SMRZZT, STATUS ) {
  let UserInfo = {
    headimgurl,
    nickname,
    openid,
    YHID,
    NAME,
    PHONENO,
    SFZH,
    SMRZZT,
    STATUS
  };
  UserInfo = JSON.stringify(UserInfo);
  localStorage.setItem("userInfo", UserInfo);
}

export function getUserInfo() {
  let UserInfo = localStorage.getItem("userInfo");
  return JSON.parse(UserInfo);
}

